                var categories = this.categoriesPoints;
                let sortable = [];
                var highestCategory;
                this.responses.forEach(function(response){
                    response.forEach(function(item){
                        if(item == "BA"){
                            categories.banker += 1;
                        }
                        else if(item == "CA"){
                            categories.capitalist += 1;
                        }
                        else if(item == "SA")
