                        }
                        else if(item == "IN"){
                            categories.influencer += 1;
                        }
                        else if(item == "AD"){
                            categories.adversary += 1;
                        }
                    });
                });

                for (var items in categories) {
                    sortable.push([items, categories[items]]);
                }

